/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
html {
  font-size: 62.5%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 1.25;
}

a {
  color: #000;
}

a:hover {
  color: #000;
}

a:focus {
  color: #000;
}

a:active {
  color: #333333;
}

a img,
img {
  max-width: 100%;
  outline: none;
  border: none;
}

.aligncenter {
  display: block;
  margin: 0 auto 10px;
  height: auto;
}

.alignright {
  display: block;
  margin: 0 auto 10px;
  height: auto;
}

@media (min-width: 768px) {
  .alignright {
    float: right;
    margin: 0 0 10px 10px;
  }
}

.alignleft {
  display: block;
  margin: 0 auto 10px;
  height: auto;
}

@media (min-width: 768px) {
  .alignleft {
    float: left;
    margin: 0 10px 10px 0;
  }
}

.container .row.row-no-gutters,
.container-fluid .row.row-no-gutters {
  margin-right: 0;
  margin-left: 0;
}

.container .row.row-no-gutters [class*="col-"],
.container-fluid .row.row-no-gutters [class*="col-"] {
  padding-right: 0;
  padding-left: 0;
}

.container .row.row-centered,
.container-fluid .row.row-centered {
  text-align: center;
  font-size: 0;
}

.container .row.row-centered [class*="col-"],
.container-fluid .row.row-centered [class*="col-"] {
  float: none;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
}

.container-fluid .row.row-no-gutters {
  margin-right: -15px;
  margin-left: -15px;
}

.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths {
  float: left;
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}

.col-xs-5ths {
  width: 20%;
}

@media (min-width: 768px) {
  .col-sm-5ths {
    width: 20%;
  }
}

@media (min-width: 992px) {
  .col-md-5ths {
    width: 20%;
  }
}

@media (min-width: 1200px) {
  .col-lg-5ths {
    width: 20%;
  }
}

@media (min-width: 1400px) {
  .col-xl-5ths {
    width: auto;
  }
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
h1,
.h1 {
  font: 700 36px/1.25 "Open Sans Condensed", sans-serif;
  margin: 0 0 10px;
}

h2,
.h2 {
  font: 700 30px/1.25 "Open Sans Condensed", sans-serif;
  margin: 0 0 10px;
}

h3,
.h3 {
  font: 700 24px/1.25 "Open Sans Condensed", sans-serif;
  margin: 0 0 10px;
}

h4,
.h4 {
  font: 700 18px/1.25 "Open Sans Condensed", sans-serif;
  margin: 0 0 10px;
}

h5,
.h5 {
  font: 700 14px/1.25 "Open Sans Condensed", sans-serif;
  margin: 0 0 10px;
}

p + h2,
p + h3,
ul + h2,
ul + h3,
ol + h2,
ol + h3 {
  margin-top: 25px;
}

p + h4,
p + h5,
ul + h4,
ul + h5,
ol + h4,
ol + h5 {
  margin-top: 20px;
}

p {
  margin: 0 0 10px;
}

/*--------------------------------------------------------------
# Buttons
--------------------------------------------------------------*/
.btn.btn-black {
  background-color: #000;
  border-color: #000;
  color: #fff;
}

.btn.btn-black:hover {
  background-color: #1a1a1a;
  border-color: #1a1a1a;
  color: #fff;
}

.btn.btn-black:focus {
  background-color: #262626;
  border-color: #262626;
  color: #fff;
}

.btn.btn-black:active:focus {
  background-color: #000;
  border-color: #000;
  color: #fff;
}

.btn.btn-white {
  background-color: #fff;
  border-color: #fff;
  color: #000;
}

.btn.btn-white:hover {
  background-color: #e6e6e6;
  border-color: #e6e6e6;
  color: #000;
}

.btn.btn-white:focus {
  background-color: #d9d9d9;
  border-color: #d9d9d9;
  color: #000;
}

.btn.btn-white:active:focus {
  background-color: #fff;
  border-color: #fff;
  color: #000;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Gallery
--------------------------------------------------------------*/
.gallery {
  text-align: center;
  margin: 0 0 15px;
  font-size: 0;
}

.gallery .gallery-item {
  display: inline-block;
  width: 100%;
  padding: 10px;
}

.gallery .thumbnail {
  display: inline-block;
  width: 100%;
  margin: 0;
  padding: 3px;
}

.gallery .thumbnail.active, .gallery .thumbnail:focus, .gallery .thumbnail:hover {
  border-color: #333333;
}

.gallery .thumbnail > img {
  width: 100%;
  margin: 0;
}

.gallery-columns-1 .gallery-item {
  width: 100%;
}

.gallery-columns-2 .gallery-item {
  width: 50%;
}

.gallery-columns-3 .gallery-item {
  width: calc(100% / 3);
}

.gallery-columns-4 .gallery-item {
  width: 25%;
}

@media (max-width: 767px) {
  .gallery-columns-4 .gallery-item {
    width: 50%;
  }
}

.gallery-columns-5 .gallery-item {
  width: 20%;
}

@media (max-width: 991px) {
  .gallery-columns-5 .gallery-item {
    width: 25%;
  }
}

@media (max-width: 767px) {
  .gallery-columns-5 .gallery-item {
    width: 50%;
  }
}

.gallery-columns-6 .gallery-item {
  width: calc(100% / 6);
}

@media (max-width: 991px) {
  .gallery-columns-6 .gallery-item {
    width: 25%;
  }
}

@media (max-width: 767px) {
  .gallery-columns-6 .gallery-item {
    width: 50%;
  }
}

.gallery-columns-7 .gallery-item {
  width: calc(100% / 7);
}

@media (max-width: 991px) {
  .gallery-columns-7 .gallery-item {
    width: 25%;
  }
}

@media (max-width: 767px) {
  .gallery-columns-7 .gallery-item {
    width: 50%;
  }
}

.gallery-columns-8 .gallery-item {
  width: 12.5%;
}

@media (max-width: 991px) {
  .gallery-columns-8 .gallery-item {
    width: 25%;
  }
}

@media (max-width: 767px) {
  .gallery-columns-8 .gallery-item {
    width: 50%;
  }
}

.gallery-columns-9 .gallery-item {
  width: calc(100% / 9);
}

@media (max-width: 991px) {
  .gallery-columns-9 .gallery-item {
    width: 25%;
  }
}

@media (max-width: 767px) {
  .gallery-columns-9 .gallery-item {
    width: 50%;
  }
}

.blueimp-gallery > .indicator > li {
  width: 40px;
  height: 30px;
  border-radius: 0;
}

@media (min-width: 992px) {
  .blueimp-gallery > .indicator > li {
    width: 60px;
    height: 45px;
  }
}

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.navbar .navbar-toggle {
  margin: 0;
  padding: 18px 15px;
  border: 0;
  border-radius: 0;
}

.navbar .navbar-toggle .icon-bar {
  border-radius: 0;
}

.navbar-default {
  background: #fff;
  border: none;
  border-radius: 0;
}

.navbar-default .navbar-brand {
  height: auto;
  color: #000;
}

.navbar-default .navbar-nav > li > a {
  color: #000;
}

.navbar-default .navbar-nav > li > a:hover, .navbar-default .navbar-nav > li > a:focus {
  color: #262626;
}

.navbar-default .navbar-nav > li.active > a,
.navbar-default .navbar-nav > li.active > a:focus,
.navbar-default .navbar-nav > li.active > a:hover {
  background: none;
  color: #262626;
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.footer-links ul {
  list-style: none;
  padding: 0;
  font-size: 0;
}

.footer-links ul li {
  display: inline-block;
}

.footer-links ul li a {
  padding: 5px 10px;
  font-size: 14px;
}

.footer-left {
  text-align: center;
}

@media (min-width: 992px) {
  .footer-left {
    text-align: left;
  }
}

.footer-right {
  text-align: center;
}

@media (min-width: 992px) {
  .footer-right {
    text-align: right;
  }
}

