/*------------------------------
	## Comments
------------------------------*/
#comment-nav ul {
  list-style: none;
  padding: 0;
  text-align: center;
}

#comment-nav ul li {
  display: inline-block;
}

#comment-nav ul li a {
  display: inline-block;
  padding: 5px;
  margin: 0 5px;
}

ol.commentlist {
  list-style: none;
  padding: 0;
}

ol.commentlist li {
  background: rgba(0, 0, 0, 0.05);
  padding: 15px 0 15px 15px;
}

ol.commentlist li div.avatar {
  float: left;
  width: 65px;
}

ol.commentlist li .comment-text {
  float: left;
  width: calc(100% - 65px);
  padding: 0 15px;
}

ol.commentlist > li {
  margin: 0 0 15px;
}

ol.commentlist li + li {
  margin-top: 15px;
}

ol.commentlist ul {
  list-style: none;
  padding: 0;
  margin: 15px 0 0;
}

ol.commentlist .comment-reply-link, ol.commentlist .comment-edit-link {
  font-size: 12px;
  float: right;
}

ol.commentlist .comment-edit-link {
  margin-right: 10px;
}

.comment-form input[type="text"],
.comment-form input[type="email"],
.comment-form textarea {
  display: block;
  width: 100%;
}

